import React, { useState, useEffect, useRef } from 'react';
import MasterLayout from '../masterLayout/MasterLayout';
import Breadcrumb from '../components/Breadcrumb';
import api from '../services/api'; 
import { toast } from 'react-toastify';
import { Icon } from "@iconify/react";
import { GoogleMap, useJsApiLoader, DrawingManager, Autocomplete, Polygon } from '@react-google-maps/api';
import { GOOGLE_MAPS_LIBRARIES } from '../components/MapConfig';

const MAP_CONTAINER_STYLE = { width: '100%', height: '550px' };
const DEFAULT_CENTER = { lat: 28.6139, lng: 77.2090 };

const LocationsTopPage = () => {
    const [areas, setAreas] = useState([]);
    const [showModal, setShowModal] = useState(false);
    const [editId, setEditId] = useState(null);
    const [selectedIndex, setSelectedIndex] = useState(null);
    
    const [formData, setFormData] = useState({ 
        name: '', 
        description: '', 
        geometrics_outline: '[]' 
    });

    const [polygons, setPolygons] = useState([]); 
    const mapRef = useRef(null);
    const autocompleteRef = useRef(null);
    
    // FIX: Store polygon references to access getPath() correctly
    const polygonRefs = useRef({});

    const { isLoaded } = useJsApiLoader({
        id: 'google-map-script',
        googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
        libraries: GOOGLE_MAPS_LIBRARIES
    });

    useEffect(() => {
        fetchAreas();
    }, []);

    const fetchAreas = async () => {
        try {
            const res = await api.get('/api/top-locations');
            setAreas(res.data);
        } catch (err) { toast.error("Failed to load areas"); }
    };

    const onPlaceChanged = () => {
        const place = autocompleteRef.current.getPlace();
        if (place.geometry) {
            mapRef.current.panTo(place.geometry.location);
            mapRef.current.setZoom(14);
        }
    };

    const onPolygonComplete = (polygon) => {
        const path = polygon.getPath().getArray().map(latLng => ({
            lat: latLng.lat(),
            lng: latLng.lng()
        }));
        const updatedPolygons = [...polygons, path];
        setPolygons(updatedPolygons);
        setFormData({ ...formData, geometrics_outline: JSON.stringify(updatedPolygons) });
        setSelectedIndex(updatedPolygons.length - 1);
        polygon.setMap(null); 
        toast.success("New shape added!");
    };

    // FIX: Use the stored ref to get the updated path
    const handlePolygonEdit = (index) => {
        const polygonInstance = polygonRefs.current[index];
        if (polygonInstance) {
            const newPath = polygonInstance.getPath().getArray().map(latLng => ({
                lat: latLng.lat(),
                lng: latLng.lng()
            }));
            const updatedPolygons = [...polygons];
            updatedPolygons[index] = newPath;
            setPolygons(updatedPolygons);
            setFormData({ ...formData, geometrics_outline: JSON.stringify(updatedPolygons) });
        }
    };

    const deleteSelectedPolygon = () => {
        if (selectedIndex === null) return;
        const updated = polygons.filter((_, i) => i !== selectedIndex);
        setPolygons(updated);
        setFormData({ ...formData, geometrics_outline: JSON.stringify(updated) });
        setSelectedIndex(null);
        toast.info("Polygon removed");
    };

    const clearShapes = () => {
        if(window.confirm("Are you sure you want to clear all shapes?")) {
            setPolygons([]);
            setFormData({ ...formData, geometrics_outline: '[]' });
            setSelectedIndex(null);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const method = editId ? 'put' : 'post';
            const url = editId ? `/api/top-locations/${editId}` : '/api/top-locations';
            await api[method](url, formData);
            toast.success("Location Saved Successfully!");
            handleClose();
            fetchAreas();
        } catch (err) { toast.error("Save failed"); }
    };

    const handleEdit = (area) => {
        setEditId(area.id);
        setFormData({
            name: area.name,
            description: area.description || '',
            geometrics_outline: area.geometrics_outline || '[]'
        });
        try {
            const parsed = JSON.parse(area.geometrics_outline || '[]');
            setPolygons(Array.isArray(parsed) ? parsed : []);
        } catch (e) { setPolygons([]); }
        setShowModal(true);
    };

    const handleClose = () => {
        setShowModal(false);
        setEditId(null);
        setPolygons([]);
        setSelectedIndex(null);
        polygonRefs.current = {}; // Clear refs
        setFormData({ name: '', description: '', geometrics_outline: '[]' });
    };

    return (
        <MasterLayout>
            <Breadcrumb title="Top Locations" />
            
            <div className="d-flex justify-content-between mb-24">
                <h6 className="mb-0">Geographical Management</h6>
                <button onClick={() => setShowModal(true)} className="btn btn-primary-600 radius-8">
                    <Icon icon="lucide:plus" className="me-1" /> Create Top Location
                </button>
            </div>

            <div className="card radius-12 border-0 shadow-sm">
                <div className="card-body p-0">
                    <table className="table mb-0">
                        <thead className="bg-neutral-50 text-secondary-light">
                            <tr>
                                <th className="ps-24 py-16">Area Name</th>
                                <th className="py-16">Description</th>
                                <th className="py-16 text-center">Shapes</th>
                                <th className="text-end pe-24 py-16">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {areas.map(area => (
                                <tr key={area.id} className="border-bottom">
                                    <td className="ps-24 fw-bold text-primary-600">{area.name}</td>
                                    <td className="text-sm text-secondary-light">{area.description || 'No description'}</td>
                                    <td className="text-center">
                                        <span className="badge bg-primary-100 text-primary-600 px-12">
                                            {JSON.parse(area.geometrics_outline || '[]').length} Poly(s)
                                        </span>
                                    </td>
                                    <td className="text-end pe-24">
                                        <button onClick={() => handleEdit(area)} className="btn btn-outline-info-600 btn-sm radius-8">
                                            <Icon icon="lucide:edit" />
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>

            {showModal && (
                <div className="modal fade show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.7)', zIndex: 1050 }}>
                    <div className="modal-dialog modal-xl modal-dialog-centered" style={{ maxWidth: '95%' }}>
                        <div className="modal-content radius-16 border-0 overflow-hidden shadow-lg">
                            <form onSubmit={handleSubmit}>
                                <div className="modal-header bg-base border-bottom p-20 d-flex justify-content-between">
                                    <h6 className="mb-0">{editId ? 'Modify Top Location' : 'New Top Location'}</h6>
                                    <button type="button" onClick={handleClose} className="btn-close"></button>
                                </div>
                                
                                <div className="modal-body p-24 bg-neutral-50">
                                    <div className="row g-4">
                                        <div className="col-lg-4">
                                            <div className="card border-0 radius-12 p-20 shadow-sm h-100">
                                                <div className="mb-20">
                                                    <label className="form-label fw-bold">Location Name</label>
                                                    <input type="text" className="form-control radius-8" value={formData.name} onChange={e => setFormData({...formData, name: e.target.value})} required placeholder="e.g. South Delhi District" />
                                                </div>
                                                <div className="mb-20">
                                                    <label className="form-label fw-bold">Description</label>
                                                    <textarea className="form-control radius-8" rows="3" value={formData.description} onChange={e => setFormData({...formData, description: e.target.value})} placeholder="Area boundary notes..." />
                                                </div>
                                                <div className="mb-0 flex-grow-1 d-flex flex-column">
                                                    <div className="d-flex justify-content-between align-items-center mb-8">
                                                        <label className="form-label fw-bold mb-0">Geometrics (JSON)</label>
                                                        <div className="d-flex gap-2">
                                                            {selectedIndex !== null && (
                                                                <button type="button" onClick={deleteSelectedPolygon} className="btn btn-sm btn-danger radius-8 px-12">Delete selected</button>
                                                            )}
                                                            <button type="button" onClick={clearShapes} className="btn btn-sm btn-outline-secondary radius-8">Clear All</button>
                                                        </div>
                                                    </div>
                                                    <textarea className="form-control text-xs font-monospace bg-neutral-50 radius-8 flex-grow-1" style={{ minHeight: '250px' }} value={formData.geometrics_outline} onChange={e => setFormData({...formData, geometrics_outline: e.target.value})} />
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-lg-8">
                                            <div className="card border-0 radius-12 p-2 shadow-sm">
                                                <div className="mb-12 p-12">
                                                    {isLoaded && (
                                                        <Autocomplete onLoad={a => autocompleteRef.current = a} onPlaceChanged={onPlaceChanged}>
                                                            <input type="text" className="form-control radius-8 ps-40" placeholder="Search a city or area to jump map..." onKeyDown={e => e.key === 'Enter' && e.preventDefault()} />
                                                        </Autocomplete>
                                                    )}
                                                </div>
                                                <div className="rounded-bottom overflow-hidden border-top">
                                                    {isLoaded ? (
                                                        <GoogleMap
                                                            mapContainerStyle={MAP_CONTAINER_STYLE}
                                                            center={DEFAULT_CENTER}
                                                            zoom={11}
                                                            onLoad={m => mapRef.current = m}
                                                            onClick={() => setSelectedIndex(null)}
                                                        >
                                                            {polygons.map((path, idx) => (
                                                                <Polygon 
                                                                    key={idx} 
                                                                    paths={path}
                                                                    onLoad={(poly) => polygonRefs.current[idx] = poly}
                                                                    options={{ 
                                                                        fillColor: selectedIndex === idx ? '#FF0000' : '#485afe', 
                                                                        fillOpacity: 0.35, 
                                                                        strokeColor: selectedIndex === idx ? '#FF0000' : '#485afe', 
                                                                        strokeWeight: 2,
                                                                        editable: selectedIndex === idx,
                                                                        draggable: selectedIndex === idx
                                                                    }} 
                                                                    onClick={(e) => { e.stop(); setSelectedIndex(idx); }}
                                                                    onDragEnd={() => handlePolygonEdit(idx)}
                                                                    onMouseUp={() => handlePolygonEdit(idx)}
                                                                />
                                                            ))}
                                                            <DrawingManager
                                                                onPolygonComplete={onPolygonComplete}
                                                                options={{
                                                                    drawingControl: true,
                                                                    drawingControlOptions: { drawingModes: ['polygon'] },
                                                                    polygonOptions: { fillColor: '#485afe', fillOpacity: 0.3, strokeColor: '#485afe', strokeWeight: 2, editable: true }
                                                                }}
                                                            />
                                                        </GoogleMap>
                                                    ) : <div className="p-40 text-center">Loading Maps...</div>}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                
                                <div className="modal-footer bg-base border-top p-20">
                                    <button type="button" onClick={handleClose} className="btn btn-outline-secondary px-32 radius-8">Discard</button>
                                    <button type="submit" className="btn btn-primary-600 px-40 radius-8">
                                        <Icon icon="solar:diskette-bold" className="me-2" /> Save Area Definition
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            )}
        </MasterLayout>
    );
};

export default LocationsTopPage;