import React, { useState, useEffect, useRef } from 'react';
import MasterLayout from '../masterLayout/MasterLayout';
import Breadcrumb from '../components/Breadcrumb';
import api from '../services/api'; 
import { toast } from 'react-toastify';
import { Icon } from "@iconify/react";
import { GoogleMap, useJsApiLoader, Autocomplete, Marker } from '@react-google-maps/api';
import { GOOGLE_MAPS_LIBRARIES } from '../components/MapConfig';

const MAP_CONTAINER_STYLE = { width: '100%', height: '100%' };
const DEFAULT_CENTER = { lat: 28.6139, lng: 77.2090 };

const TABS = [
    "Basic Info", "Location", "Description", "Communication", 
    "Opening Hours", "Amenities", "Media", "Google Sync", 
    "Attributes", "Accessibility", "Services", "Health & Safety", "Payments"
];

const LocationsMainPage = () => {
    // 1. Initialize states as empty arrays to prevent .map() errors
    const [locations, setLocations] = useState([]);
    const [parentAreas, setParentAreas] = useState([]);
    const [categories, setCategories] = useState([]); 
    
    const [showModal, setShowModal] = useState(false);
    const [activeTab, setActiveTab] = useState("Basic Info");
    const [editId, setEditId] = useState(null);

    const [formData, setFormData] = useState({
        location_name: '', top_location_id: '', internal_id: '',
        street_address: '', city: '', state: '', postal_code: '', country: 'IN',
        lat: '', lng: '', phone_number: '', email: '', website_url: '',
        description: '', primary_category_id: '', additional_category: '',
        opening_hours: {}, sync_status: 'PENDING'
    });

    const mapRef = useRef(null);
    const autocompleteRef = useRef(null);

    const { isLoaded } = useJsApiLoader({
        id: 'google-map-script',
        googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
        libraries: GOOGLE_MAPS_LIBRARIES
    });

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const [locs, parents, cats] = await Promise.all([
                api.get('/api/main-locations'),
                api.get('/api/options/top-locations'),
                api.get('/api/options/categories')
            ]);

            // 2. Extract data safely using fallback empty arrays
            setLocations(locs.data?.data || locs.data || []);
            setParentAreas(parents.data || []);
            setCategories(cats.data || []); 
            
        } catch (err) { 
            console.error("Fetch Error:", err);
            toast.error("Error loading location data"); 
        }
    };

    const onPlaceChanged = () => {
        const place = autocompleteRef.current.getPlace();
        if (place.geometry) {
            const newLat = place.geometry.location.lat();
            const newLng = place.geometry.location.lng();
            
            let addr = { street: '', city: '', state: '', zip: '' };
            place.address_components?.forEach(c => {
                if (c.types.includes('locality')) addr.city = c.long_name;
                if (c.types.includes('administrative_area_level_1')) addr.state = c.long_name;
                if (c.types.includes('postal_code')) addr.zip = c.long_name;
                if (c.types.includes('route')) addr.street = c.long_name;
            });

            setFormData(prev => ({ 
                ...prev, lat: newLat, lng: newLng, 
                street_address: addr.street || place.name, 
                city: addr.city, state: addr.state, postal_code: addr.zip 
            }));
            mapRef.current.panTo({ lat: newLat, lng: newLng });
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const url = editId ? `/locations/${editId}` : '/locations';
            await api[editId ? 'put' : 'post'](url, formData);
            toast.success("Business Profile Saved Successfully!");
            handleClose();
            fetchData();
        } catch (err) { toast.error(err.response?.data?.error || "Save failed"); }
    };

    const handleEdit = (loc) => {
        setEditId(loc.id);
        setFormData(loc);
        setShowModal(true);
    };

    const handleClose = () => {
        setShowModal(false);
        setEditId(null);
        setActiveTab("Basic Info");
        setFormData({
            location_name: '', top_location_id: '', internal_id: '',
            street_address: '', city: '', state: '', postal_code: '', country: 'IN',
            lat: '', lng: '', phone_number: '', email: '', website_url: '',
            description: '', primary_category_id: '', additional_category: '',
            opening_hours: {}, sync_status: 'PENDING'
        });
    };

    return (
        <MasterLayout>
            <Breadcrumb title="Main Locations" />

            <div className="d-flex justify-content-between align-items-center mb-24">
                <h6 className="mb-0">Business Profiles</h6>
                <button onClick={() => setShowModal(true)} className="btn btn-primary-600 radius-8">
                    <Icon icon="lucide:plus" className="me-1" /> Add New Business
                </button>
            </div>

            <div className="card radius-12 border-0 shadow-sm">
                <div className="table-responsive">
                    <table className="table mb-0">
                        <thead className="bg-neutral-50 text-secondary-light">
                            <tr>
                                <th className="ps-24">Store Code</th>
                                <th>Business Name</th>
                                <th>Parent Area</th>
                                <th>Sync Status</th>
                                <th className="text-end pe-24">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {/* Safety check: Use Array.isArray */}
                            {Array.isArray(locations) && locations.map(loc => (
                                <tr key={loc.id} className="border-bottom">
                                    <td className="ps-24 text-secondary-light">{loc.internal_id}</td>
                                    <td className="fw-bold">{loc.location_name}</td>
                                    <td>{loc.top_location_name}</td>
                                    <td>
                                        <span className={`badge ${loc.sync_status === 'SYNCED' ? 'bg-success-100 text-success' : 'bg-warning-100 text-warning'}`}>
                                            {loc.sync_status}
                                        </span>
                                    </td>
                                    <td className="text-end pe-24">
                                        <button onClick={() => handleEdit(loc)} className="btn btn-sm btn-outline-info-600 me-2">
                                            <Icon icon="lucide:edit" />
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>

            {showModal && (
                <div className="modal fade show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.7)', zZndex: 1050 }}>
                    <div className="modal-dialog modal-xl modal-dialog-centered" style={{ maxWidth: '92%' }}>
                        <div className="modal-content radius-16 overflow-hidden border-0">
                            
                            <div className="modal-header bg-base border-bottom p-24">
                                <h6 className="mb-0">{editId ? 'Edit Profile' : 'New Business Profile'}</h6>
                                <button onClick={handleClose} className="btn-close"></button>
                            </div>

                            <div className="d-flex flex-column flex-lg-row" style={{ height: '72vh' }}>
                                {/* Sidebar Tabs */}
                                <div className="bg-neutral-50 border-end overflow-auto" style={{ width: '240px' }}>
                                    {TABS.map(tab => (
                                        <button 
                                            key={tab} 
                                            className={`w-100 text-start px-24 py-16 border-0 fw-medium transition-all ${activeTab === tab ? 'bg-primary-600 text-white' : 'bg-transparent text-secondary'}`}
                                            onClick={() => setActiveTab(tab)}
                                        >
                                            {tab}
                                        </button>
                                    ))}
                                </div>

                                {/* Content Area */}
                                <div className="flex-grow-1 overflow-auto bg-base p-32">
                                    <form id="mainLocForm" onSubmit={handleSubmit}>
                                        {activeTab === "Basic Info" && (
                                            <div className="row g-4">
                                                <div className="col-md-6">
                                                    <label className="form-label fw-bold">Business Name*</label>
                                                    <input type="text" className="form-control" value={formData.location_name} onChange={e => setFormData({...formData, location_name: e.target.value})} required />
                                                </div>
                                                <div className="col-md-6">
                                                    <label className="form-label fw-bold">Internal Store Code</label>
                                                    <input type="text" className="form-control" value={formData.internal_id} onChange={e => setFormData({...formData, internal_id: e.target.value})} />
                                                </div>
                                                <div className="col-md-6">
                                                    <label className="form-label fw-bold">Top Location (Area)</label>
                                                    <select className="form-select" value={formData.top_location_id} onChange={e => setFormData({...formData, top_location_id: e.target.value})}>
                                                        <option value="">-- Select Area --</option>
                                                        {Array.isArray(parentAreas) && parentAreas.map(p => (
                                                            <option key={p.id} value={p.id}>{p.name}</option>
                                                        ))}
                                                    </select>
                                                </div>
                                                <div className="col-md-6">
                                                    <label className="form-label fw-bold">Primary Category</label>
                                                    <select className="form-select" value={formData.primary_category_id} onChange={e => setFormData({...formData, primary_category_id: e.target.value})}>
                                                        <option value="">-- Select Category --</option>
                                                        {/* 3. Safety check for the categories map */}
                                                        {Array.isArray(categories) && categories.map(c => (
                                                            <option key={c.id} value={c.id}>{c.category_name || c.name}</option>
                                                        ))}
                                                    </select>
                                                </div>
                                            </div>
                                        )}

                                        {activeTab === "Location" && (
                                            <div className="row g-4 h-100">
                                                <div className="col-md-4">
                                                    <label className="form-label fw-bold">Google Maps Search</label>
                                                    {isLoaded && (
                                                        <Autocomplete onLoad={a => autocompleteRef.current = a} onPlaceChanged={onPlaceChanged}>
                                                            <input type="text" className="form-control shadow-sm mb-16" placeholder="Search address..." onKeyDown={e => e.key === 'Enter' && e.preventDefault()}/>
                                                        </Autocomplete>
                                                    )}
                                                    <div className="mb-12">
                                                        <label className="form-label text-xs">Street Address</label>
                                                        <input type="text" className="form-control" value={formData.street_address} onChange={e => setFormData({...formData, street_address: e.target.value})} />
                                                    </div>
                                                    <div className="row g-2">
                                                        <div className="col-6">
                                                            <label className="form-label text-xs">City</label>
                                                            <input type="text" className="form-control" value={formData.city} onChange={e => setFormData({...formData, city: e.target.value})} />
                                                        </div>
                                                        <div className="col-6">
                                                            <label className="form-label text-xs">Postal Code</label>
                                                            <input type="text" className="form-control" value={formData.postal_code} onChange={e => setFormData({...formData, postal_code: e.target.value})} />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-md-8 rounded border overflow-hidden" style={{ minHeight: '400px' }}>
                                                    {isLoaded ? (
                                                        <GoogleMap 
                                                            mapContainerStyle={MAP_CONTAINER_STYLE} 
                                                            center={formData.lat ? {lat: parseFloat(formData.lat), lng: parseFloat(formData.lng)} : DEFAULT_CENTER} 
                                                            zoom={15} 
                                                            onLoad={m => mapRef.current = m}
                                                        >
                                                            {formData.lat && <Marker position={{lat: parseFloat(formData.lat), lng: parseFloat(formData.lng)}} />}
                                                        </GoogleMap>
                                                    ) : <div>Loading Map...</div>}
                                                </div>
                                            </div>
                                        )}

                                        {/* Dynamic placeholder for other tabs */}
                                        {!["Basic Info", "Location"].includes(activeTab) && (
                                            <div className="text-center py-50">
                                                <Icon icon="solar:box-minimalistic-bold-duotone" width="64" className="text-neutral-300 mb-16" />
                                                <h5 className="text-secondary">{activeTab} Configuration</h5>
                                                <p className="text-secondary-light">Specific GMB attributes for this section can be configured here.</p>
                                            </div>
                                        )}
                                    </form>
                                </div>
                            </div>

                            <div className="modal-footer bg-base border-top p-24">
                                <button type="button" onClick={handleClose} className="btn btn-outline-secondary-600 px-32">Discard</button>
                                <button type="submit" form="mainLocForm" className="btn btn-primary-600 px-40">
                                    <Icon icon="solar:diskette-bold" className="me-2" /> Save Business Profile
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </MasterLayout>
    );
};

export default LocationsMainPage;