import axios from 'axios';

// Create a custom instance
const api = axios.create({
    baseURL: '', // Using the proxy defined in package.json
});

// REQUEST INTERCEPTOR: Automatically adds the token
api.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem('token');
        if (token) {
            // Attach the token in the format the backend expects
            config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// RESPONSE INTERCEPTOR: Handles session expiration
api.interceptors.response.use(
    (response) => response,
    (error) => {
        // If the server returns 401 (Unauthorized) or 403 (Forbidden)
        if (error.response && (error.response.status === 401 || error.response.status === 403)) {
            console.warn("Session expired or invalid. Logging out...");
            
            // Clear local session
            localStorage.removeItem('token');
            localStorage.removeItem('user');
            
            // Force redirect to sign-in
            window.location.href = '/signin';
        }
        return Promise.reject(error);
    }
);

export default api;