import React, { useEffect } from 'react';
import api from '../services/api';
import { useAuth } from '../context/AuthContext';
import { toast } from 'react-toastify';
import { Icon } from "@iconify/react";
const SignInPage = () => {
    const { login } = useAuth();

    const handleCallbackResponse = async (response) => {
        try {
            // Send the Google JWT (credential) to your backend
            const res = await axios.post('/api/auth/google-signin', {
                credential: response.credential
            });

            if (res.data.success) {
                login(res.data.user, res.data.token);
                toast.success(`Welcome, ${res.data.user.name}`);
            }
        } catch (err) {
            toast.error("Google Sign-In failed.");
        }
    };

    useEffect(() => {
        /* global google */
        if (window.google) {
            google.accounts.id.initialize({
                client_id: process.env.REACT_APP_GOOGLE_CLIENT_ID,
                callback: handleCallbackResponse
            });

            google.accounts.id.renderButton(
                document.getElementById("googleSignInBtn"),
                { theme: "outline", size: "large", width: "320" }
            );
        }
    }, []);

    return (
        <div className="auth-bg min-vh-100 d-flex align-items-center justify-content-center bg-light">
            <div className="card shadow-lg radius-16 p-40 text-center" style={{ width: '400px' }}>
                <Icon icon="solar:map-point-bold-duotone" className="text-primary-600 mb-16" style={{ fontSize: '48px' }} />
                <h4 className="mb-8 fw-bold">LoQation Management</h4>
                <p className="text-secondary mb-32">Please sign in to access your dashboard</p>
                
                <div id="googleSignInBtn" className="d-flex justify-content-center"></div>
                
                <p className="mt-32 text-xs text-secondary">
                    Use your authorized company Google account to continue.
                </p>
            </div>
        </div>
    );
};

export default SignInPage;